import React from 'react';
import './About.css';
import lannaPolytechnicImage from './image/it.jpg';

function About() {
  return (
    <div className="container">
      <img src={lannaPolytechnicImage} alt="Lanna Polytechnic" className="about-image" />
      <div className="about-text">
        <p>สาขาเทคโนโลยีสารสนเทศของวิทยาลัยโปลิ เป็นสาขาที่มีความสำคัญและเป็นที่ต้องการในยุคปัจจุบัน เนื่องจากเทคโนโลยีสารสนเทศมีบทบาทสำคัญในทุกภาคส่วนของสังคม ไม่ว่าจะเป็นธุรกิจ การศึกษา การแพทย์ การบริการ และอื่น ๆ การเรียนในสาขานี้จะเปิดโอกาสให้นักศึกษาได้เรียนรู้ทักษะและความรู้ที่จำเป็นต่อการทำงานในโลกดิจิทัล</p>
        <p>สาขาเทคโนโลยีสารสนเทศของวิทยาลัยโปลิเป็นสาขาที่เตรียมนักศึกษาให้พร้อมเข้าสู่โลกดิจิทัลที่มีการเปลี่ยนแปลงและพัฒนาอย่างรวดเร็ว ด้วยหลักสูตรที่ทันสมัยและครอบคลุมทักษะที่สำคัญในยุคปัจจุบัน นักศึกษาจะได้รับความรู้และทักษะที่จำเป็นต่อการทำงานในหลากหลายสาขาอาชีพ</p>
      </div>
    </div>
  );
}

export default About;