import React from 'react';
import './Courses.css';

function Courses() {
  return (
    <div className="container">
      <h2>หลักสูตรสาขาเทคโนโลยีสารสนเทศ</h2>
      <p>สาขาเทคโนโลยีสารสนเทศ  มีการเรียนการสอนที่ทันสมัยเน้นการฝึกทักษะการใช้งานเทคโนโลยีที่จำเป็นในยุคดิจิทัล เพื่อเตรียมความพร้อมให้กับนักศึกษาในการทำงานในอุตสาหกรรมเทคโนโลยีสารสนเทศ</p>
      
      <h3>หลักสูตรที่เปิดสอน</h3>
      <ul>
        <li><strong>หลักสูตรวิทยาการคอมพิวเตอร์ (Bachelor of Computer Science)</strong> - หลักสูตรเน้นการศึกษาด้านการพัฒนาโปรแกรม, การจัดการฐานข้อมูล, การพัฒนาแอปพลิเคชัน, และการรักษาความปลอดภัยในระบบคอมพิวเตอร์</li>
        <li><strong>หลักสูตรเทคโนโลยีสารสนเทศ (Bachelor of Information Technology)</strong> - หลักสูตรที่เน้นการฝึกทักษะการใช้งานเทคโนโลยีสารสนเทศ เช่น การพัฒนาเว็บไซต์, ระบบเครือข่าย, การพัฒนาแอปพลิเคชันมือถือ, และการวิเคราะห์ข้อมูล</li>
        <li><strong>หลักสูตรวิศวกรรมซอฟต์แวร์ (Bachelor of Software Engineering)</strong> - การออกแบบและพัฒนาซอฟต์แวร์ที่มีประสิทธิภาพ รวมถึงการพัฒนาเครื่องมือและเทคนิคในการสร้างระบบซอฟต์แวร์ที่สามารถใช้งานได้ในโลกจริง</li>
        <li><strong>หลักสูตรการพัฒนาแอปพลิเคชันมือถือ (Mobile Application Development)</strong> - หลักสูตรที่มุ่งเน้นการพัฒนาแอปพลิเคชันสำหรับอุปกรณ์มือถือทั้งในระบบ iOS และ Android</li>
      </ul>

      <h3>ทักษะที่นักศึกษาจะได้รับ</h3>
      <ul>
        <li>การพัฒนาโปรแกรมและซอฟต์แวร์ต่างๆ</li>
        <li>การวิเคราะห์และออกแบบฐานข้อมูล</li>
        <li>การพัฒนาแอปพลิเคชันมือถือ</li>
        <li>การรักษาความปลอดภัยของระบบคอมพิวเตอร์</li>
        <li>การวิเคราะห์ข้อมูลและการใช้เทคโนโลยี Big Data</li>
      </ul>
    
      <h3 className="opportunities">โอกาสในการทำงานหลังจากสำเร็จการศึกษา</h3>
      <p>นักศึกษาที่จบหลักสูตรนี้จะมีโอกาสทำงานในหลากหลายสาขา อาทิ เช่น การพัฒนาซอฟต์แวร์, การพัฒนาแอปพลิเคชันมือถือ, การดูแลและพัฒนาระบบเครือข่าย, การรักษาความปลอดภัยทางไซเบอร์, การวิเคราะห์ข้อมูล และอื่นๆ ในบริษัทเทคโนโลยีชั้นนำและองค์กรต่างๆ</p>
    </div>
  );
}

export default Courses;
